.btn {
	@extend %btn;
	border: 4px solid #7534ff;
	color: #fff;
	line-height: 1;
	background-color: transparent;
	@include panton-bold;
	padding: 29px 101px;
	font-size: 18px;
	&:hover,
	&:focus {
		top: 5px;
		&:after {
			display: none; } }
	&:after {
		content: '';
		display: block;
		position: absolute;
		left: -2px;
		right: -2px;
		top: 100%;
		margin-top: -4px;
		height: 10px;
		border-bottom-left-radius: 8px;
		border-bottom-right-radius: 8px;
		background-color: #7534ff; } }
.btn-menu {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 58px;
	margin: auto;
	width: 38px;
	height: 32px;
	background: none;
	@include vertical;
	text-align: left;
	@include trans;
	&:before,
	&:after {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		height: 4px;
		width: 100%;
		background-color: #0189ff; }
	&:before {
		top: 0; }
	&:after {
		bottom: 0; }
	&:hover {
		height: 36px;
		span {
			width: 100%; } }
	span {
		display: inline-block;
		vertical-align: middle;
		width: 12px;
		height: 4px;
		background-color: #0189ff;
		@include trans; } }
.btn-main {
	@extend %button-basic;
	padding: 21px 51px;
	border: 2px solid white;
	border-radius: 5px;
	@include roboto-bold;
	font-size: 30px;
	line-height: 1;
	color: #fff;
	background: none;
	&:hover,
	&:focus {
		top: 5px;
		&:after {
			display: none; } }
	&:after {
		content: '';
		display: block;
		position: absolute;
		left: -2px;
		right: -2px;
		top: 100%;
		margin-top: -1px;
		height: 6px;
		border-bottom-left-radius: 8px;
		border-bottom-right-radius: 8px;
		background-color: #fff; } }
.btn-category {
	@extend %button-basic;
	min-width: 282px;
	text-transform: lowercase;
	@include roboto-bold;
	font-size: 30px;
	line-height: 1;
	color: rgba(#fff, 0.4);
	word-break: break-word;
	background: none;
	&:hover {
		color: rgba(#fff, 1);
		&:before {
			width: 100%; }
		&:after {
			height: 100%; } }
	&:before,
	&:after {
		content: '';
		display: block;
		position: absolute;
		border-style: solid;
		border-color: #fff;
		transition: all 0.3s;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0; }
	&:before {
		width: 0;
		border-width: 4px 0 4px 0; }
	&:after {
		border-width: 0 4px 0 4px;
		height: 0; }
	&:nth-of-type(odd) {
		margin-right: 28px; }
	span {
		position: relative;
		z-index: 2; }
	&__inner {
		border: 4px solid rgba(#fff, 0.4);
		padding: 26px 15px; } }
.btn-back {
	@extend %button-basic;
	font-size: 24px;
	color: rgba(#fff, 0.4);
	transition: all 0.3s;
	padding-left: 30px;
	line-height: 1.4;
	&:before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		transition: all 0.3s;
		@include arr(14, 20, rgba(#fff, 0.4), l); }
	&:hover {
		color: #fff;
		&:before {
			border-right-color: #fff; } } }
